<template>
  <div v-if="isLoading" class="loading">
    <div class="loading">
      <img class="loading-img" src="https://hbimg.huabanimg.com/1cd0c5b05f630a1cfd7a056ead30db9f0776cc9e51fe3-MpXEQu">
    </div>
  </div>
  <div class="detail-content" v-else>
    <div class="clearfix top">
      <div class="content-left">
        <img :src="'https://images.weserv.nl/?url='+imageAddress" />
      </div>
      <div class="content-right">
        <div class="movieName">电影名称:</div>
        <div class="title">{{title}}</div>
        <div class="grade">评分:</div>
        <div class="resolve">{{rating}}</div>
      </div>
    </div>
    <div class="introduce">
      <p>电影介绍</p>
      <div class="movie-introduce">{{summary}}</div>
    </div>
    <div class="comments">
      <p>评论</p>
      <ul>
        <li v-for="item in commentsArr" :key="item.id" class="com">
          <span class="author">{{item.author.name}}:</span><span>{{item.summary}}</span>
        </li>
      </ul>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: "movieDetail",
  data() {
    return {
      imageAddress: "",
      title: '',
      rating: '',
      summary: '',
      commentsArr : [],
      isLoading: true
    };
  },
  methods: {
    getData(id) {
      axios
        .get(
          "https://bird.ioliu.cn/v2?url=http://api.douban.com/v2/movie/subject/" +
            id +
            "?apikey=0df993c66c0c636e29ecbb5344252a4a"
        )
        .then((res) => {
          this.imageAddress = res.data.images.large;
          this.title = res.data.original_title;
          this.rating = res.data.rating.average;
          this.summary = res.data.summary;
          this.commentsArr = res.data.popular_reviews;
          this.isLoading = false;
        })
        .catch(() => {
          return false;
        });
    }
  },
  created() {
    this.getData(this.$route.params.id);
  }
};
</script>
<style scope>
.clearfix::after {
  content: "";
  display: block;
  clear: both;
}
li{
  list-style: none;
}
.detail-content {
  position: relative;
  font-size: 0.3rem;
  padding-top: .2rem;
}
.detail-content .content-left,
.detail-content .content-right {
  float: left;
  padding-left: 3%;
}
.detail-content .content-left{
  width: 30%;
}
.detail-content .content-left img{
  width: 100%;
  padding-bottom: 10%;
}
.detail-content .content-right{
  width: 60%;
  height: 170px;
  font-weight: border;
}
.movieName,.grade{
  font-size: .5rem;
  color: orangered;
}
.content-right div{
  padding-top: 11px;
  text-align: center;
}
.resolve,.title{
  color: #757575;
}
.introduce,.comments{
  width: 100%;
  color: #757575
}
ul{
  padding: 0;
}
.author{
  color: orange;
  font-size: .3rem
}
.com{
  padding: 5px 0;
}
.loading{
  width: 100vw;
  height: 83vh;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 1rem;
}
.loading-img{
  padding: 0;
  width: 200px;
  height: 100px;
  border-radius: 10px;
  position: absolute;
  top:50%;
  left: 50%;
  transform: translate(-50%,-50%)
}
.introduce p,.comments p{
  font-size: .4rem;
  color: rgb(235, 59, 59)
}
.top{
  border-bottom: 1px solid #666;
}
.introduce{
  border-bottom: 1px solid #666;
}
</style>
